<template>
  <div class="v-gallery">
    <swiper :options="swiperOption">
        <swiper-slide v-for="(image,index) in images" :key=index>
            <img class="v-image-tag" :src="image.tag" />
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
import "swiper/dist/css/swiper.css";
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  name: "ImageGallery",
  components: {
    swiper,
    swiperSlide
  },
  data() {
    return {
      images: [
        { tag: require("@/assets/images/image1.jpg") },
        { tag: require("@/assets/images/image2.jpg") },
        { tag: require("@/assets/images/image3.jpg") },
        { tag: require("@/assets/images/image4.jpg") }
      ],
      swiperOption: {
        spaceBetween: 30,
        centeredSlides: true,
        autoplay: {
          delay: 4000,
          disableOnInteraction: false
        },
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        mousewheel: true,
        keyboard: {
          enabled: true
        },
        loop: true,
        slidesPerView: 1
      }
    };
  }
};
</script>

<style>
/*For mobile phones*/
@media only screen and (max-width: 768px) {
  .v-image-tag{
    width:25rem;
    height:15rem;
  }
}

@media only screen and (min-width: 768px) {
  .v-image-tag{
    width:50rem;
    height:25rem;
  }
}
</style>



