<template>
  <div>
    <Header />
    <div id="info-box" ref="infoBox" class="about">
      <div class="div-name">
        <p class="name">{{name}}</p>
      </div>
      <AboutMe />
    </div>
  </div>
</template>

<script>
import Header from "./Header";
import AboutMe from "./AboutMe";

export default {
  name: "About",
  components: {
    Header,
    AboutMe
  },
  data() {
    return {
      name: "Carmen Sánchez Luque",
      address: "MACLUQUE School of English. Tesoro, 14 - Malasaña"
    };
  }
};
</script>

<style>
@font-face {
  font-family: "Signature of the Ancient";
  src: url("../assets/fonts/Signature_of_the_Ancient.woff");
  font-weight: bold;
}

.name {
  font-family: "Signature of the Ancient";
  font-size: 3rem;
  text-decoration: underline;
  -webkit-text-underline-position: under;
  -ms-text-underline-position: below;
  text-underline-position: under;
}

.about {
  margin-top: 2rem;
  width: 100%;
}

/*For mobile phones*/
@media only screen and (max-width: 768px) {
  .about {
    height: 36.35rem;
    margin-top: 1rem;
    width: 100%;
  }

  .name {
    font-family: "Signature of the Ancient";
    font-size: 2rem;
    text-decoration: underline;
    -webkit-text-underline-position: under;
    -ms-text-underline-position: below;
    text-underline-position: under;
  }
}
</style>



